}
.sim-icontext.round .fa {
    width: 1.5em;
    height: 1.5em;
    margin: -0.75em 0;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-shadow: 0 0 0 .125em;
            box-shadow: 0 0 0 .125em;
    border-radius: 50%;
}



